










// 引入外部组件与ts
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/inspection/api";

@Component
export default class SensorsInfoList extends Vue {
  @Prop({ type: Number, required: true, default: 0 })
  partId!: any;

  sensorsInfoListData: Array<Object> = [];

  getSensorsInfoList(partId: any) {
    this.$http.get(api.getSensorsListByPart, { params: { partsId: partId } }).then((res: any) => {
      if (res.status) {
        res.data.forEach((item: any) => {
          this.$http.get(api.getSensorInfoById, { params: { id: item } }).then((response: any) => {
            if (response.data.status) {
              this.sensorsInfoListData.push(response.data.sensor);
            } else {
              this.$message.error(response.data.message);
            }
          });
        });
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  @Watch("partId", { deep: false })
  setPartId(newVal: any) {
    this.getSensorsInfoList(newVal);
  }
}
