









































































































































































// 引入外部组件与ts
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import PartsListTable from "./components/PartsListTable.vue";
import SensorsInfoList from "./components/SensorsInfoList.vue";
import api from "@/microApps/inspection/api";
// import CodeEditStep from "@/views/itinerant_detective/components/CodeEditStep.vue";
@Component({
  components: { PartsListTable, SensorsInfoList }
})
export default class SingleCode extends Vue {
  codePreData: any = {}; //接收数据
  pageTitle: String = "";
  disabled: Boolean = false;
  codeBasicForm: any = {};
  bindPartForm: any = {};
  bindTemplateForm: any = [];
  partChoose: Boolean = false;
  templateEditDialog: Boolean = false;
  templateFormIndex: any = 0;
  stepActive: any = 1;
  templateFormItem: any = {};

  // 获取二维码详情
  getCodeInfo() {
    this.$http.get(api.getCodeById + this.codePreData.codeId).then((res: any) => {
      if (res.data.status) {
        this.codeBasicForm = res.data.data;
        this.bindPartForm = res.data.data.deviceInfo;
        this.bindTemplateForm = res.data.data.templatePersonGroup;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }
  // 根据设备列表返回值填充设备表单
  fillPartForm(partInfo: any) {
    this.bindPartForm = partInfo;
    this.partChoose = false;
    if (partInfo.partsStatus === 1) {
      this.bindPartForm.partsStatus = "有效";
    } else {
      this.bindPartForm.partsStatus = "无效";
    }
  }
  // 根据方案选择组件返回值填充方案表单
  fillTemplateForm(tag: any) {
    this.templateEditDialog = false;
    this.bindTemplateForm[this.templateFormIndex].templateName = tag.planInfo.name;
    this.bindTemplateForm[this.templateFormIndex].templateId = tag.planInfo.id;
    this.bindTemplateForm[this.templateFormIndex].templateRemark = tag.planInfo.remark;
    this.bindTemplateForm[this.templateFormIndex].personGroups = tag.personGroups[0].groupName;
    this.bindTemplateForm[this.templateFormIndex].personGroupIds.push(tag.personGroups[0].id);
    if (tag.personGroups.length > 1) {
      for (let i = 1; i < tag.personGroups.length; i++) {
        this.bindTemplateForm[this.templateFormIndex].personGroups =
          this.bindTemplateForm[this.templateFormIndex].personGroups + "," + tag.personGroups[i].groupName;
        this.bindTemplateForm[this.templateFormIndex].personGroupIds.push(tag.personGroups[i].id);
      }
    }
  }
  // 添加巡检方案
  addTemplatePersonGroup() {
    this.bindTemplateForm.push({
      templateId: "",
      personGroupIds: []
    });
  }
  // 编辑/修改 巡检方案
  editTemplatePersonGroup(item: any, index: any) {
    this.stepActive = 1;
    this.templateFormItem = item;
    this.templateFormIndex = index;
    this.templateEditDialog = true;
  }
  // 移除已选择的巡检方案
  removeTemplatePersonGroup(item: any) {
    let index = this.bindTemplateForm.indexOf(item);
    if (index !== -1) {
      this.bindTemplateForm.splice(index, 1);
    }
  }
  // 保存二维码信息
  saveCodeInfo() {
    let requestForm: any = {};
    requestForm.catalogId = this.codePreData.catalogId;
    requestForm.codeName = this.codeBasicForm.codeName;
    requestForm.coordinate = this.codeBasicForm.coordinate;
    requestForm.location = this.codeBasicForm.location;
    requestForm.remark = this.codeBasicForm.remark;
    requestForm.effectiveRadius = this.codeBasicForm.effectiveRadius;
    requestForm.deviceInfoId = this.bindPartForm.id;
    requestForm.templatePersonGroup = this.bindTemplateForm;
    if (this.codePreData.mode === "edit") {
      requestForm.id = this.codePreData.codeId;
      this.$http.post(api.updateCode, requestForm).then((res: any) => {
        if (res.data.status) {
          this.$message.success("修改二维码信息成功！");
          this.changeSingleCodeVisible();
        } else {
          this.$message.error(res.data.message);
        }
      });
    } else {
      this.$http.post(api.saveCode, requestForm).then((res: any) => {
        if (res.data.status) {
          this.$message.success("保存二维码信息成功！");
          this.changeSingleCodeVisible();
        } else {
          this.$message.error(res.data.message);
        }
      });
    }
  }
  // 改变页面显示
  changeSingleCodeVisible() {
    this.$emit("changeVisible", false);
  }

  changeEditMode() {
    // 判断编辑模式
    if (this.codePreData.mode == "edit") {
      this.pageTitle = "修改二维码";
      this.getCodeInfo();
    } else if (this.codePreData.mode === "view") {
      this.pageTitle = "二维码详情";
      this.getCodeInfo();
      this.disabled = true;
    } else {
      this.pageTitle = "新建二维码";
    }
  }

  created() {
    let preData = this.$route.params.preData;
    if (preData) {
      this.codePreData = JSON.parse(preData);
      this.changeEditMode();
    } else {
      this.$router.back();
    }
  }
}
