








































// 引入外部组件与ts
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/inspection/api";

@Component({
  filters: {
    partsStatusFilter: function(val: any) {
      if (val === 1) {
        return "有效";
      } else {
        return "无效";
      }
    },
    bindStatusFilter: function(val: any) {
      if (val === 1) {
        return "已绑定";
      } else {
        return "未绑定";
      }
    }
  }
})
export default class PartsListTable extends Vue {
  partsTableList: Array<Object> = [];
  searchDTO: any = {
    page: 1,
    rows: 10,
    total: 0,
    condition: ""
  };

  handleCurrentChange(val: any) {
    this.$emit("chosenPartInfo", val);
  }
  getPartsList() {
    this.$http.post(api.getPartsList, this.searchDTO).then((res: any) => {
      if (res.data.status) {
        this.partsTableList = res.data.list;
        this.searchDTO.page = res.data.currentPage;
        this.searchDTO.rows = res.data.pageSize;
        this.searchDTO.total = res.data.totalResult;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  mounted() {
    this.getPartsList();
  }
}
